.app {
  display: flex;
  margin: 0;
  flex-direction: column;
  justify-content: center;
}

.screen-display {
  margin-right: 8px;
  height: 120px;
  font-size: 23px;
  text-align: right;
  display: flex;
  flex-direction: column;
  vertical-align: bottom;
}



.keyboard {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  background: rgba(255, 255, 255, 0.8);
}

.keyboard-item {
  text-align: center;
  border: 1px lightgray solid;
  font-size: 30px;
  padding: 8px;
}
